body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  /*background-image: url("./home-bg.jpg");*/
  height: 1000px;
}

.profile-avatar{
  width: 20%;
  height: 100px;
}

.menu{
  position: fixed;
  padding: 0;
  height: 0;
  top: 0;
  z-index: 100;
  width: 100%;
  height: 70px;
  transition: all .5s;
}

.menu .menu__wrapper {
  z-index: 1;
  margin-top: 50px;
  transition: all .3s;
}

.menu .menu__wrapper nav {
  font-size: 14px;
  line-height: 20px;
  font-family: Roboto Mono,monospace;
  text-transform: uppercase;
  font-weight: 700;
}
nav {
  font-size: 12px;
  line-height: 18px;
  font-family: Roboto Mono,monospace;
  text-transform: uppercase;
  font-weight: 700;
  margin-bottom: 30px;
}
nav ul li {
  display: inline-block;
}

.menu .menu__wrapper nav a {
  color: #fff;
  opacity: 1;
}

nav ul li a {
  padding-right: 50px;
  color: #999;
}
a {
  color: #6d56c1;
  transition: all .3s;
  position: relative;
  text-decoration: none;
  cursor: pointer;
}
.menu .menu__wrapper nav a:after {
  background: #fff;
}
.menu .menu__wrapper nav a:after {
  background: #fff;
}
nav ul li a:after {
  content: "";
  position: absolute;
  width: 7px;
  height: 2px;
  bottom: 0;
  right: 42px;
  opacity: 0;
  transition: background .3s;
  background: #6d56c1;
}

.menu .menu__mobile-button {
  color: #fff;
  opacity: .5;
  transition: all .3s;
  background-color: transparent;
  border: none;
}
.menu.menu--active {
  background-color: #fff;
  box-shadow: 0 5px 8px 0 rgba(0,0,0,.14);
}
.menu.menu--active .menu__wrapper a {
  color: #555!important;
  opacity: 1;
}
.menu.menu--active .menu__wrapper {
  margin-top: 25px;
}

.top-section{
  background-image: url("./bg-main.jpg");
  background-color: #6d56c1;
  background-repeat: no-repeat;
  background-position: 50%;
  background-size: cover;
  position: relative;
  margin-bottom: 40px;
}

.top-section:before{
  background: linear-gradient(54deg,rgba(74,76,179,.95) 34%,hsla(0,34%,71%,.95));
  height: 100%;
  left: 0;
  right: 0;
  position: absolute;
  content: "";
  z-index: 0;
}

.personal-profile {
  color: #fff;
  font-family: Roboto Mono,monospace;
  padding-top: 90px;
}

.personal-profile .personal-profile__avatar img {
  border-radius: 5px;
  box-shadow: 0 0 27px rgba(96,96,96,.34);
  width: 90%;
  height: 380px;
  -o-object-fit: cover;
  object-fit: cover;
  margin-top: 10px;
  position: relative;
  top: 40px;
  -webkit-filter: grayscale(100%); /* Safari 6.0 - 9.0 */
  filter: grayscale(100%);
}

.personal-profile .personal-profile__name {
  font-size: 44px;
  line-height: 50px;
  font-weight: 700;
  margin-top: 40px;
  margin-bottom: 10px;
}

.personal-profile .personal-profile__work {
  font-size: 16px;
  line-height: 24px;
  margin-bottom: 40px;
}

.personal-profile .personal-profile__contacts {
  margin-bottom: 20px;
}

.contact-list {
  font-size: 16px;
  line-height: 24px;
  font-family: Roboto Mono,monospace;
}

.contact-list.contact-list__opacity-titles dt {
  opacity: .4;
}

.contact-list dt {
  float: left;
  font-weight: 700;
  text-transform: uppercase;
}
.contact-list dd {
  padding-left: 100px;
  margin-bottom: 15px;
}

.contact-list dd a {
  color: #fff;
}

.personal-profile .personal-profile__social {
  margin-bottom: 35px;
}
